	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import gangweileixing from '@/views/gangweileixing/list'
	import ziyuanleixing from '@/views/ziyuanleixing/list'
	import examquestion from '@/views/exam/examquestion/list'
	import tijiaoceshi from '@/views/tijiaoceshi/list'
	import jiaoshi from '@/views/jiaoshi/list'
	import chengjifenxi from '@/views/chengjifenxi/list'
	import kaogonggangwei from '@/views/kaogonggangwei/list'
	import exampaper from '@/views/exampaper/list'
	import shenlunceshi from '@/views/shenlunceshi/list'
	import storeup from '@/views/storeup/list'
	import ceshipingfen from '@/views/ceshipingfen/list'
	import yonghu from '@/views/yonghu/list'
	import chat from '@/views/chat/list'
	import kaogongziyuan from '@/views/kaogongziyuan/list'
	import config from '@/views/config/list'
	import exampaperlist from '@/views/exam/exampaperlist/list'
	import examination from '@/views/exam/exampaperlist/examination'
	import examrecord from '@/views/exam/examrecord/list'
	import examfailrecord from '@/views/exam/examfailrecord/list'
	import jiaoshiRegister from '@/views/jiaoshi/register'
	import jiaoshiCenter from '@/views/jiaoshi/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/jiaoshiCenter',
			name: '',
			component: jiaoshiCenter
		}
		,{
			path: '/news',
			name: '',
			component: news
		}
		,{
			path: '/gangweileixing',
			name: '',
			component: gangweileixing
		}
		,{
			path: '/ziyuanleixing',
			name: '',
			component: ziyuanleixing
		}
		,{
			path: '/examquestion',
			name: '',
			component: examquestion
		}
		,{
			path: '/tijiaoceshi',
			name: '',
			component: tijiaoceshi
		}
		,{
			path: '/jiaoshi',
			name: '',
			component: jiaoshi
		}
		,{
			path: '/chengjifenxi',
			name: '',
			component: chengjifenxi
		}
		,{
			path: '/kaogonggangwei',
			name: '',
			component: kaogonggangwei
		}
		,{
			path: '/exampaper',
			name: '',
			component: exampaper
		}
		,{
			path: '/shenlunceshi',
			name: '',
			component: shenlunceshi
		}
		,{
			path: '/storeup',
			name: '',
			component: storeup
		}
		,{
			path: '/ceshipingfen',
			name: '',
			component: ceshipingfen
		}
		,{
			path: '/yonghu',
			name: '',
			component: yonghu
		}
		,{
			path: '/chat',
			name: '',
			component: chat
		}
		,{
			path: '/kaogongziyuan',
			name: '',
			component: kaogongziyuan
		}
		,{
			path: '/config',
			name: '',
			component: config
		}
		, {
			path: '/exampaperlist',
			name: '',
			component: exampaperlist
		}, {
			path: '/examrecord',
			name: '',
			component: examrecord
		}, {
			path: '/examfailrecord',
			name: '',
			component: examfailrecord
		}
		]
	},
	{
		path: '/jiaoshiRegister',
		name: '',
		component: jiaoshiRegister
	},
	{
		path: '/examination',
		name: '',
		component: examination
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
